/* import styled from '@emotion/styled'; */


/* ! 1.Стили для устройств с шириной экрана от 320px до 479px */
@media screen and (min-width: 320px) and (max-width: 479px) {
    .shoppingCartContainer {
        position: relative;
        
        width: 90vw;
        height: 80vh;
        
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 20px;

        padding: 20px;
        padding: 20px 20px 30px 20px;
        margin-left: auto;
        margin-right: auto;
        margin-top: 20px;
        margin-bottom: 20px;

        font-size: 18px;
        font-weight: 600;

        border-radius: 8px;
        background-color: #fae2e2;
        box-shadow: 12px 12px 18px rgba(59, 43, 14, 0.23);

        /* outline: 1px solid blue; */
    }
    

    .сustomerData {
        width: 95%;
        height: 20vh;
        min-height: 220px;

        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        border: 2px solid rgb(164, 214, 255);
        border-radius: 8px;
        box-shadow: 12px 12px 18px rgba(59, 43, 14, 0.23);

        /* outline: 1px solid blue; */
    }

    .CustomerOrder {
        overflow-y: auto;
        width: 95%;
        height: 40vh;

        border: 2px solid rgb(164, 214, 255);
        border-radius: 8px;
        box-shadow: 12px 12px 18px rgba(59, 43, 14, 0.23);

        /* outline: 1px solid blue; */
    }
}


