/* ! 3.Стили для устройств с шириной экрана от 768px и выше */
@media screen and (min-width: 768px) {
    .shoppingCartContainer {
        position: relative;

        width: 90%;
        height: 80%;

        display: flex;
        gap: 20px;

        padding: 20px;
        margin-left: auto;
        margin-right: auto;
        margin-top: 20px;
        margin-bottom: 20px;

        font-size: 18px;
        font-weight: 600;

        border-radius: 8px;
        background-color: #fae2e2;
        box-shadow: 12px 12px 18px rgba(59, 43, 14, 0.23);

        /* outline: 1px solid blue; */
    }
    

    .сustomerData {
        flex-basis: 30vw;
        min-width: 300px;

        height: 100%;
        height: 83%;

        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        border: 2px solid rgb(164, 214, 255);
        border-radius: 8px;
        box-shadow: 12px 12px 18px rgba(59, 43, 14, 0.23);

        /* outline: 1px solid blue; */
    }

    .CustomerOrder {
        flex-grow: 1;
        overflow-y: auto;

        width: 60%;
        height: 83%;

        border: 2px solid rgb(164, 214, 255);
        border-radius: 8px;
        box-shadow: 12px 12px 18px rgba(59, 43, 14, 0.23);

        /* outline: 1px solid blue; */
    }
}







